import type { CRMRecord, ProtectRecord, SettlementRecord } from "./types"

export interface FilterOptions {
    searchQuery: string
    statusFilter: string
    partnerFilter: string
    dateRangeStart: string
    dateRangeEnd: string
}

export function filterRecords(records: CRMRecord[], options: FilterOptions): CRMRecord[] {
    const { searchQuery, statusFilter, partnerFilter, dateRangeStart, dateRangeEnd } = options

    return records.filter((r) => {
        // Search Filter
        let matchesSearch = searchQuery === ""
        if (!matchesSearch) {
            const query = searchQuery.toLowerCase()
            const commonSearch =
                r.name.toLowerCase().includes(query) ||
                r.mobileNumber.includes(query)

            if (r.type === "protect") {
                matchesSearch = commonSearch || (r as ProtectRecord).accountNumber.includes(query)
            } else if (r.type === "settlement") {
                matchesSearch = commonSearch || (r as SettlementRecord).loanAccNo.includes(query)
            } else if (r.type === "nexus") {
                // For Nexus, search by valid fields like email or userId if available
                // NexusRecord has: email, userId
                matchesSearch = commonSearch ||
                    (r as any).email?.toLowerCase().includes(query) ||
                    (r as any).userId?.toLowerCase().includes(query)
            }
        }

        // Status Filter
        const matchesStatus = statusFilter === "all" || r.status === statusFilter

        // Partner Filter
        const matchesPartner = partnerFilter === "all" || r.partner === partnerFilter

        // Date Filter
        let matchesDate = true
        if (dateRangeStart && dateRangeEnd) {
            const recordDate = new Date(r.formFilledDate)
            matchesDate = recordDate >= new Date(dateRangeStart) && recordDate <= new Date(dateRangeEnd)
        }

        return matchesSearch && matchesStatus && matchesPartner && matchesDate
    })
}
